<template>
  <div class="lost-item-register">
    <h1>üìù ÊãõÈ¢ÜÁôªËÆ∞</h1>

    <el-form
      :model="form"
      ref="formRef"
      label-width="120px"
      :rules="rules"
      label-position="top"
    >
      <!-- Ë°®ÂçïÈ°π -->
      <el-form-item label="Áâ©ÂìÅÊ†áÈ¢ò" prop="title">
        <el-input
          v-model="form.title"
          placeholder="ËØ∑ËæìÂÖ•Áâ©ÂìÅÂêçÁß∞ÔºàÂ¶ÇÔºöÈªëËâ≤Âçé‰∏∫ÊâãÊú∫Ôºâ"
        />
      </el-form-item>

      <el-form-item label="ËØ¶ÁªÜÊèèËø∞" prop="description">
        <el-input
          type="textarea"
          :rows="4"
          v-model="form.description"
          placeholder="ËØ∑Â∞ΩÂèØËÉΩËØ¶ÁªÜÊèèËø∞Áâ©ÂìÅÁâπÂæÅÔºàÂ¶ÇÔºöÂûãÂè∑„ÄÅÁâπÊÆäÊ†áËÆ∞Á≠âÔºâ"
          show-word-limit
          maxlength="300"
        />
      </el-form-item>

      <!-- Êó•ÊúüÈÄâÊã© -->
      <el-row :gutter="20">
        <el-col :span="12">
          <el-form-item label="ÊãæÂèñÊó∂Èó¥" prop="lost_time">
            <el-date-picker
              v-model="form.lost_time"
              type="datetime"
              format="yyyy-MM-dd HH:mm"
              value-format="yyyy-MM-ddTHH:mm"
              placeholder="ÈÄâÊã©ÂÖ∑‰ΩìÊó∂Èó¥"
            />
          </el-form-item>
        </el-col>
        <el-col :span="12">
          <el-form-item label="ÊãæÂèñÂú∞ÁÇπ" prop="location">
            <div class="location-input-wrapper">
              <el-input
                v-model="form.location"
                placeholder="‰æãÂ¶ÇÔºö3Âè∑ÊïôÂ≠¶Ê•º201ÊïôÂÆ§"
              />
              <el-button
                type="primary"
                icon="el-icon-map-location"
                @click="showMapDialog"
                class="map-btn"
                circle
              >
              </el-button>
            </div>
          </el-form-item>
        </el-col>
      </el-row>

      <!-- ÂàÜÁ±ªÈÄâÊã© -->
      <el-form-item label="Áâ©ÂìÅÂàÜÁ±ª" prop="category" width="100%">
        <!-- ‰øÆÊîπel-cascaderÁöÑoptionsÁªëÂÆö -->
        <el-cascader
          v-model="form.category"
          :options="categoryTreeOptions"
          :props="{
            checkStrictly: true,
            expandTrigger: 'hover',
            emitPath: false,
          }"
          placeholder="ËØ∑ÈÄâÊã©ÊúÄÂåπÈÖçÁöÑÂàÜÁ±ª"
          style="width: 50%"
          @change="handleCategoryChange"
        />
      </el-form-item>

      <!-- ËÅîÁ≥ªÊñπÂºè -->
      <el-form-item label="ËÅîÁ≥ªÊñπÂºè" prop="contact">
        <el-input
          v-model="form.contact"
          placeholder="ÊâãÊú∫Âè∑ÊàñÈÇÆÁÆ±"
          style="width: 50%"
        />
      </el-form-item>

      <!-- ÂõæÁâá‰∏ä‰º† -->
      <el-form-item label="Áâ©ÂìÅÁÖßÁâá">
        <el-upload
          action="#"
          list-type="picture-card"
          :auto-upload="false"
          :on-change="handleUploadSuccess"
          :file-list="form.images"
          :before-upload="beforeUpload"
        >
          <i class="el-icon-plus" />
        </el-upload>
        <div class="el-upload__tip">
          ÊîØÊåÅ‰∏ä‰º† JPG/PNG Ê†ºÂºèÂõæÁâáÔºåÂçïÂº†‰∏çË∂ÖËøá5MB
        </div>
      </el-form-item>

      <!-- Êìç‰ΩúÊåâÈíÆ -->
      <el-form-item>
        <el-button
          type="primary"
          class="submit-btn"
          :loading="isSubmitting"
          @click="submitForm"
        >
          {{ isSubmitting ? "Êèê‰∫§‰∏≠..." : "Á´ãÂç≥ÁôªËÆ∞" }}
        </el-button>
      </el-form-item>
    </el-form>
    <!-- Âú®Ê®°ÊùøÂ∫ïÈÉ®Ê∑ªÂä†Ê≠§ÂØπËØùÊ°Ü -->
    <el-dialog
      title="‚úÖ ÁôªËÆ∞ÊàêÂäü"
      :visible.sync="dialogVisible"
      width="700px"
      @closed="handleDialogClosed"
    >
      <el-descriptions :column="2" border label-class-name="detail-label">
        <!-- ÂºπÁ™óÂÜÖÂÆπ -->
        <el-descriptions-item label="Áâ©ÂìÅÊ†áÈ¢ò">{{
          submittedItem.title
        }}</el-descriptions-item>
        <el-descriptions-item label="ÂàÜÁ±ª">
          {{ getCategoryName(submittedItem.category) }}
        </el-descriptions-item>
        <el-descriptions-item label="ÊãæÂèñÊó∂Èó¥">
          {{ submittedItem.lost_time }}
        </el-descriptions-item>
        <el-descriptions-item label="‰∏¢Â§±Âú∞ÁÇπ">{{
          submittedItem.location
        }}</el-descriptions-item>
        <el-descriptions-item label="ËÅîÁ≥ªÊñπÂºè" :span="2">
          <el-link type="primary">{{ submittedItem.contact }}</el-link>
        </el-descriptions-item>
        <el-descriptions-item label="ËØ¶ÁªÜÊèèËø∞" :span="2">
          <pre class="description-pre">{{ submittedItem.description }}</pre>
        </el-descriptions-item>
        <el-descriptions-item
          label="Áâ©ÂìÅÁÖßÁâá"
          :span="2"
          v-if="submittedItem.images?.length"
        >
          <el-image
            v-for="(img, index) in submittedItem.images"
            :key="index"
            :src="img.url"
            fit="cover"
            class="detail-image"
          />
        </el-descriptions-item>
      </el-descriptions>
      <!-- Â∫ïÈÉ®Êìç‰ΩúÊåâÈíÆ -->
      <span slot="footer">
        <el-button type="success" @click="handleConfirm">
          ÊâìÂç∞ÂõûÊâß (Ctrl+P)
        </el-button>
        <el-button type="primary" @click="dialogVisible = false">
          Á°ÆÂÆöËøîÂõû
        </el-button>
      </span>
    </el-dialog>
    <!-- Ê∑ªÂä†Âú∞ÂõæÂºπÁ™ó -->
    <!-- Âú®Âú∞ÂõæÂºπÁ™ó‰∏≠Ê∑ªÂä†Âä†ËΩΩÁä∂ÊÄÅ -->
    <el-dialog
      title="ËØ∑Âú®Âú∞Âõæ‰∏äÈÄâÊã©‰ΩçÁΩÆ"
      :visible.sync="mapDialogVisible"
      width="80%"
    >
      <div id="map-container" style="height: 500px; position: relative">
        <div v-if="mapLoading" class="map-loading">
          <i class="el-icon-loading"></i> Ê≠£Âú®Ëé∑ÂèñÂú∞ÂùÄ‰ø°ÊÅØ...
        </div>
      </div>
      <span slot="footer">
        <el-button @click="mapDialogVisible = false">ÂèñÊ∂à</el-button>
        <el-button type="primary" @click="confirmLocation">Á°ÆÂÆö</el-button>
      </span>
    </el-dialog>
  </div>
</template>


<script>
import axios from "axios";
import AMapLoader from "@amap/amap-jsapi-loader";

export default {
  /* eslint-disable no-undef */
  data() {
    return {
      // Êñ∞Â¢ûÊâãÊú∫Âè∑È™åËØÅËßÑÂàô
      phoneRules: [
        { required: true, message: "ËØ∑ËæìÂÖ•ËÅîÁ≥ªÊñπÂºè", trigger: "blur" },
        {
          pattern: /^(1[3-9]\d{9}|[\w-]+@[\w-]+\.[\w-]+)$/,
          message: "ËØ∑ËæìÂÖ•ÊúâÊïàÁöÑÊâãÊú∫Âè∑ÊàñÈÇÆÁÆ±",
          trigger: "blur",
        },
      ],
      form: {
        title: "",
        description: "",
        lost_time: null,
        location: "",
        category: null,
        contact: "",
        images: [],
        location_lng: null, // Êñ∞Â¢ûÁªèÂ∫¶Â≠óÊÆµ
        location_lat: null, // Êñ∞Â¢ûÁ∫¨Â∫¶Â≠óÊÆµ
      },
      categories: [],
      categoriesTree: [], // Êñ∞Â¢û‰∏Ä‰∏™Áî®‰∫éÂ≠òÂÇ®Ê†ëÂΩ¢ÁªìÊûÑÊï∞ÊçÆÁöÑÊï∞ÁªÑ
      categoryTreeOptions: [], // Áî®‰∫éÂ≠òÂÇ®Ê†ëÂΩ¢ÁªìÊûÑÈÄâÈ°πÁöÑÊï∞ÁªÑ
      categoryList: [], // Â≠òÂÇ®ÂàÜÁ±ªÂàóË°®
      fileList: [],
      rules: {
        title: [{ required: true, message: "ËØ∑ËæìÂÖ•Ê†áÈ¢ò", trigger: "blur" }],
        description: [
          { required: true, message: "ËØ∑ËæìÂÖ•ËØ¶ÁªÜÊèèËø∞", trigger: "blur" },
        ],
        lost_time: [
          { required: true, message: "ËØ∑ÈÄâÊã©ÊãæÂèñÊó∂Èó¥", trigger: "change" },
        ],
        location: [{ required: true, message: "ËØ∑ËæìÂÖ•Âú∞ÁÇπ", trigger: "blur" }],
        category: [
          { required: true, message: "ËØ∑ÈÄâÊã©Áâ©ÂìÅÂàÜÁ±ª", trigger: "change" },
        ],
        contact: [
          { required: true, message: "ËØ∑ËæìÂÖ•ËÅîÁ≥ªÊñπÂºè", trigger: "blur" },
        ],
      },
      isSubmitting: false,
      dialogVisible: false,
      submittedItem: {}, // Â≠òÂÇ®ÊúçÂä°Âô®ËøîÂõûÁöÑÂÆåÊï¥Êï∞ÊçÆ
      printStyle: `
      @media print {
        body * { visibility: hidden; }
        .el-dialog { width: 210mm!important; visibility: visible; }
        .el-dialog__header, .el-dialog__footer { display: none; }
      }
    `,
      mapDialogVisible: false,
      selectedLocation: {
        lng: null,
        lat: null,
        address: "",
      },
      map: null,
      geocoder: null, // È´òÂæ∑Âú∞ÂõæÁöÑGeocoderÂØπË±°
      mapLoading: false, // Êñ∞Â¢ûÂú∞ÂõæÂä†ËΩΩÁä∂ÊÄÅ
      marker: null, // Âú∞ÂõæÊ†áËÆ∞ÂØπË±°
      infoWindow: null, // ‰ø°ÊÅØÁ™óÂè£ÂØπË±°
    };
  },
  mounted() {
    this.fetchCategories().then(() => {
      this.categoryTreeOptions = this.convertToCascaderOptions(
        this.categoriesTree
      );
    });
    window._AMapSecurityConfig = {
      securityJsCode: "c684b8bc9a42d62c059edd9fee411dce",
    };
  },
  methods: {
    // ‰øÆÊîπconvertToCascaderOptionsÊñπÊ≥ï
    convertToCascaderOptions(data) {
      return data.map((item) => ({
        value: item.id,
        label: item.name,
        children: item.children.length
          ? this.convertToCascaderOptions(item.children)
          : undefined,
      }));
    },
    getCSRFToken() {
      const cookieValue =
        document.cookie
          .split("; ")
          .find((row) => row.startsWith("csrftoken="))
          ?.split("=")[1] || "";
      return cookieValue;
    },
    async fetchCategories() {
      try {
        const response = await axios.get("/api/found/categories/tree/", {
          headers: {
            Authorization: `Token ${this.$store.state.token}`,
            "X-CSRFToken": this.getCSRFToken(),
          },
        });
        this.categoriesTree = response.data;
      } catch (error) {
        console.error("Ëé∑ÂèñÁâ©ÂìÅÂàÜÁ±ªÂ§±Ë¥•:", error);
      }
    },
    handleCategoryChange(value) {
      // Â§ÑÁêÜÂàÜÁ±ªÈÄâÊã©ÂèòÂåñ
      console.log("ÈÄâÊã©ÁöÑÂàÜÁ±ª:", value);
    },
    // Êñ∞Â¢ûÂú∞ÂõæÁõ∏ÂÖ≥ÊñπÊ≥ï
    showMapDialog() {
      this.mapDialogVisible = true;
      this.$nextTick(() => {
        const mapContainer = document.getElementById("map-container");
        // Â§çÁî®ÈÄªËæëÊîπ‰∏∫Êìç‰ΩúDOMÊòæÁ§∫Áä∂ÊÄÅ
        if (this.map) {
          mapContainer.style.visibility = "visible";
          this.map.setFitView();
          return;
        }
        this.initAMap();
      });
    },

    async initAMap() {
      try {
        if (this.map) return;
        this.mapLoading = true;
        // Âº∫Âà∂Á¶ÅÁî®ÁºìÂ≠ò
        const loaderConfig = {
          key: "3958565d98f73366bc8f766bcc44cb66",
          version: "2.0",
          plugins: ["AMap.Geocoder", "AMap.Scale", "AMap.ToolBar"],
          securityJsCode: "c684b8bc9a42d62c059edd9fee411dce",
          AMapUI: {
            version: "1.1",
            plugins: [],
          },
          url: `https://webapi.amap.com/maps?v=2.0&t=${Date.now()}`,
        };

        // Âä†ËΩΩÂâçÊ£ÄÊü•ÂÖ®Â±ÄAMapÂØπË±°
        if (window.AMap) {
          window.AMap = undefined;
          delete window.AMap;
        }

        // Â¢ûÂä†Âä†ËΩΩË∂ÖÊó∂Â§ÑÁêÜ
        const AMap = await Promise.race([
          AMapLoader.load(loaderConfig),
          new Promise((_, reject) =>
            setTimeout(() => reject(new Error("Âú∞ÂõæÂä†ËΩΩË∂ÖÊó∂")), 5000)
          ),
        ]);

        // Â¢ûÂä†ÂÆâÂÖ®Ê†°È™å
        if (!AMap?.Map) {
          throw new Error("È´òÂæ∑Âú∞ÂõæAPIÂä†ËΩΩÂºÇÂ∏∏");
        }
        // ÂàùÂßãÂåñÂú∞Âõæ
        this.initMapCore(AMap);
        this.initMapComponents(AMap);
      } catch (error) {
        console.error("Âú∞ÂõæÂä†ËΩΩÂ§±Ë¥•:", error);
        this.$message.error(`Âú∞ÂõæÂàùÂßãÂåñÂ§±Ë¥•: ${error.message}`);
        this.cleanupMap();
      } finally {
        this.mapLoading = false;
      }
    },
    cleanupMap() {
      if (this.map) {
        // ÁßªÈô§ÊâÄÊúâ‰∫ã‰ª∂ÁõëÂê¨
        this.map.off("click", this.handleMapClick);
        // Ê∏ÖÈô§ÊâÄÊúâË¶ÜÁõñÁâ©
        this.map.clearMap();
        // ÈîÄÊØÅÂú∞ÂõæÂÆû‰æã
        this.map.destroy();
        this.map = null;
      }
      // ÂÖ∂‰ªñÁõ∏ÂÖ≥ÂÆû‰æãÁΩÆÁ©∫
      this.geocoder = null;
      this.marker = null;
      if (this.infoWindow) {
        this.infoWindow.close();
        this.infoWindow = null;
      }
      // Âº∫Âà∂Ê∏ÖÈô§DOMÂÆπÂô®
      const container = document.getElementById("map-container");
      if (container) {
        container.innerHTML = "";
      }
    },
    resetMap() {
      const mapContainer = document.getElementById("map-container");
      if (this.map) {
        mapContainer.style.visibility = "hidden"; // ÈöêËóèÂú∞ÂõæÂÆπÂô®
        this.map.clearMap(); // ‰ªÖÊ∏ÖÈô§Ë¶ÜÁõñÁâ©
      }
      this.marker = null;
      if (this.infoWindow) {
        this.infoWindow.close();
        this.infoWindow = null;
      }
    },
    initMapCore(AMap) {
      this.map = new AMap.Map("map-container", {
        zoom: 15,
        center: new AMap.LngLat(112.662198, 37.745788),
        resizeEnable: true,
        animateEnable: true,
        touchZoomCenter: 1,
      });
    },
    initMapComponents(AMap) {
      // ÂàùÂßãÂåñÂú∞ÁêÜÁºñÁ†ÅÂô®
      this.geocoder = new AMap.Geocoder({
        city: "ÂÖ®ÂõΩ",
        timeout: 5000,
        extensions: "all",
      });

      // Ê∑ªÂä†Êéß‰ª∂
      this.map.addControl(new AMap.ToolBar({ position: "LT" }));
      this.map.addControl(new AMap.Scale({ position: "LB" }));

      // Ëá™ÂÆö‰πâ‰ø°ÊÅØÁ™óÂè£
      this.infoWindow = new AMap.InfoWindow({
        offset: new AMap.Pixel(0, -30),
        isCustom: true,
        autoMove: true,
        closeWhenClickMap: true,
      });

      // ÁªëÂÆöÂú∞Âõæ‰∫ã‰ª∂
      this.map.on("click", this.handleMapClick);
    },
    handleMapClick(e) {
      try {
        this.mapLoading = true;
        // Ê∏ÖÈô§ÊóßÊ†áËÆ∞
        if (this.marker) {
          this.map.remove(this.marker);
        }
        // Ê∑ªÂä†Êñ∞Ê†áËÆ∞
        this.marker = new AMap.Marker({
          position: e.lnglat,
          title: "ÈÄâÊã©ÁöÑ‰ΩçÁΩÆ",
        });
        this.map.add(this.marker);

        // Ëá™Âä®Â±Ö‰∏≠
        this.map.setCenter(e.lnglat);
        this.geocoder.getAddress(e.lnglat, (status, result) => {
          try {
            this.mapLoading = false;

            if (status === "complete" && result.info === "OK") {
              const address = result.regeocode.formattedAddress;
              this.selectedLocation = {
                lng: e.lnglat.getLng(),
                lat: e.lnglat.getLat(),
                address: address,
              };

              // ÂÆûÊó∂Êõ¥Êñ∞ËæìÂÖ•Ê°Ü
              this.form.location = address;

              // ÊòæÁ§∫‰ø°ÊÅØÁ™óÂè£
              // <!-- Âú®handleMapClick‰∏≠Êõ¥Êñ∞‰ø°ÊÅØÁ™óÂè£ÂÜÖÂÆπ -->
              this.infoWindow.setContent(`
<div class="map-info">
  <div class="info-header">
    <i class="el-icon-location"></i>
    <h3>üìç Â∑≤Á°ÆËÆ§‰ΩçÁΩÆ</h3>
  </div>
  <div class="info-content">
    <div class="address-line">
      <span class="label">ËØ¶ÁªÜÂú∞ÂùÄÔºö</span>
      <span class="value">${address}</span>
    </div>
    <div class="coordinate">
      <span class="lng">ÁªèÂ∫¶ ${e.lnglat.getLng().toFixed(6)}</span>
      <span class="lat">Á∫¨Â∫¶ ${e.lnglat.getLat().toFixed(6)}</span>
    </div>
  </div>
</div>`);

              this.infoWindow.open(this.map, e.lnglat);
            } else {
              console.error("Âú∞ÂùÄËß£ÊûêÂ§±Ë¥•:", result);
              this.$message.warning("Êó†Ê≥ïËé∑ÂèñËØ•‰ΩçÁΩÆÂú∞ÂùÄÔºåËØ∑ÈáçÊñ∞ÈÄâÊã©");
            }
          } catch (err) {
            console.error("Âú∞ÂùÄËß£ÊûêÂºÇÂ∏∏:", err);
            this.$message.warning("Âú∞ÂùÄËß£ÊûêÊúçÂä°ÂºÇÂ∏∏");
          }
        });
      } catch (error) {
        this.mapLoading = false;
        console.error("Âú∞ÂùÄËß£ÊûêÂºÇÂ∏∏:", error);
        this.$message.warning("‰ΩçÁΩÆÈÄâÊã©ËøáÁ®ãÂèëÁîüÈîôËØØ");
      }
    },
    // ‰øÆÊîπÂêéÁöÑÁ°ÆËÆ§ÊñπÊ≥ï
    confirmLocation() {
      if (!this.selectedLocation.address) {
        this.$message.warning("ËØ∑ÂÖàÂú®Âú∞Âõæ‰∏äÈÄâÊã©‰ΩçÁΩÆ");
        return;
      } else {
        this.form.location = this.selectedLocation.address;
        // Á°Æ‰øùËµãÂÄºÁªôformÁöÑÁªèÁ∫¨Â∫¶Â≠óÊÆµ
        this.form.location_lng = this.selectedLocation.lng;
        this.form.location_lat = this.selectedLocation.lat;
      }

      // Â∑≤ÁªèÂÆûÊó∂Êõ¥Êñ∞ÔºåËøôÈáåÂè™ÈúÄË¶ÅÂÖ≥Èó≠ÂºπÁ™ó
      this.mapDialogVisible = false;

      // Ê∏ÖÈô§Âú∞ÂõæÂÖÉÁ¥†
      if (this.marker) {
        this.map.remove(this.marker);
      }
      this.infoWindow.close();
    },

    // ÂàÜÁ±ªIDËΩ¨ÂêçÁß∞ÔºàÂåπÈÖçÁî®Êà∑ÂéüÊúâÂàÜÁ±ªÊï∞ÊçÆÔºâ
    getCategoryName(categoryId) {
      const category = this.categoryList.find((item) => item.id === categoryId);
      return category ? category.name : "Êú™Áü•ÂàÜÁ±ª";
    },

    // Â§ÑÁêÜÂºπÁ™óÂÖ≥Èó≠ÂêéÁöÑÊìç‰Ωú
    handleDialogClosed() {
      this.$router.go(-1); // ÊàñËá™ÂÆö‰πâË∑≥ËΩ¨ÈÄªËæë
    },

    // ÊâìÂç∞ÂäüËÉΩ
    handleConfirm() {
      const printWindow = window.open("", "_blank");
      printWindow.document.write(`
      <style>${this.printStyle}</style>
      ${document.querySelector(".el-dialog").outerHTML}
    `);
      printWindow.print();
      printWindow.close();
    },

    // ‰ºòÂåñÂêéÁöÑÊèê‰∫§ÊñπÊ≥ï
    async submitForm() {
      try {
        this.isSubmitting = true;
        // Áªü‰∏ÄÊèê‰∫§Êï∞ÊçÆÔºàÂê´ÂõæÁâáÔºâ
        const formData = new FormData();
        Object.keys(this.form).forEach((key) => {
          if (key !== "images") {
            // Â§ÑÁêÜÊâÄÊúâÈùûÂõæÁâáÂ≠óÊÆµÔºàÂåÖÂê´ÁªèÁ∫¨Â∫¶Ôºâ
            const value = this.form[key];

            // Â§ÑÁêÜÁ©∫ÂÄºÊÉÖÂÜµ
            if (value === null || value === undefined) {
              formData.append(key, "");
            } else {
              formData.append(key, value);
            }
          }
        });
        // Ê∑ªÂä†ÂõæÁâáÊñá‰ª∂
        this.form.images.forEach((file) => {
          formData.append("images", file.raw);
        });
        const response = await axios.post("/api/items/found/", formData, {
          headers: {
            "Content-Type": "multipart/form-data",
            Authorization: `Token ${this.$store.state.token}`,
          },
        });
        // ‰øùÂ≠òÊúçÂä°Âô®ËøîÂõûÁöÑÂÆåÊï¥Êï∞ÊçÆ
        this.submittedItem = {
          ...response.data,
          images: this.form.images, // ‰øùÁïôÂâçÁ´Ø‰∏¥Êó∂È¢ÑËßàÂõæ
        };

        // Ê∏ÖÁ©∫Ë°®ÂçïÔºàÊ†πÊçÆÈúÄÊ±ÇÈÄâÊã©‰øùÁïôÊàñÊ∏ÖÈô§Ôºâ
        this.$refs.formRef.resetFields();
        this.form.images = [];

        this.dialogVisible = true; // ÊòæÁ§∫ÂºπÁ™ó

        this.$message.success({
          message: "ÁôªËÆ∞ÊàêÂäüÔºå3ÁßíÂêéËá™Âä®Ë∑≥ËΩ¨",
          duration: 3000,
        });
      } catch (error) {
        const msg = error.response?.data?.detail || "Êèê‰∫§Â§±Ë¥•ÔºåËØ∑Ê£ÄÊü•ÁΩëÁªúËøûÊé•";
        this.$message.error(msg);
      } finally {
        this.isSubmitting = false;
      }
    },

    // Â¢ûÂº∫ÁöÑÊñá‰ª∂‰∏ä‰º†Â§ÑÁêÜ
    handleUploadSuccess(file) {
      if (!file || !file.raw) {
        this.$message.error("Êñá‰ª∂Âä†ËΩΩÂºÇÂ∏∏");
        return;
      }
      try {
        // ÊòæÁ§∫10ÁßíÂÜÖÊúâÊïàÁöÑÈ¢ÑËßàÈìæÊé•
        const previewUrl = URL.createObjectURL(file.raw);
        const fileData = {
          uid: file.uid, // ÂøÖÈ°ªÂåÖÂê´uid
          name: file.name,
          status: "ready", // ÊâãÂä®ÁÆ°ÁêÜÁä∂ÊÄÅ
          percentage: 0, // ËøõÂ∫¶Êù°ÂàùÂßãÂÄº
          url: previewUrl,
          raw: file.raw,
        };

        this.form.images = [...this.form.images, fileData];
      } catch (error) {
        console.error("Êñá‰ª∂È¢ÑËßàÈîôËØØ:", error);
        this.$message.error("‰∏çÊîØÊåÅËØ•Êñá‰ª∂Á±ªÂûã");
      }
    },

    beforeUpload(file) {
      const isValidType = ["image/jpeg", "image/png"].includes(file.type);
      const isLt5M = file.size / 1024 / 1024 < 5;
      if (!isValidType) {
        this.$message.error("‰ªÖÊîØÊåÅ JPG/PNG Ê†ºÂºè");
        return false;
      }
      if (!isLt5M) {
        this.$message.error("ÂõæÁâáÂ§ßÂ∞è‰∏çËÉΩË∂ÖËøá5MB");
        return false;
      }
      return true;
    },
  },
  async created() {
    try {
      // Ëé∑ÂèñÂàÜÁ±ªÂàóË°®
      const response = await axios.get("/api/found/categories/");
      this.categoryList = response.data;
    } catch (error) {
      console.error("Ëé∑ÂèñÂàÜÁ±ªÂàóË°®Â§±Ë¥•:", error);
    }
  },
  beforeDestroy() {
    if (this.map) {
      // Â¢ûÂº∫ÂÖ®Â±ÄÂØπË±°Ê∏ÖÁêÜ
      if (window.AMap) {
        window.AMap.Map = null; // Ê∏ÖÈô§Ê†∏ÂøÉÁ±ªÂºïÁî®
        window.AMap = undefined;
        delete window.AMap;
        console.log("ÂÖ®Â±ÄAMapÊ∏ÖÁêÜÂÆåÊàê:", typeof window.AMap); // È™åËØÅÊ∏ÖÁêÜÁªìÊûú
      }
      console.log("Âú∞ÂõæÂÆû‰æãÈîÄÊØÅÂâçÁä∂ÊÄÅÔºö", this.map.getStatus());
      this.map.off();
      this.map.clearMap();
      this.map.destroy(true);

      // Áªü‰∏ÄDOMÂ§ÑÁêÜÊñπÂºèÔºàÊîπ‰∏∫‰∏éFoundItemRegister.vue‰∏ÄËá¥Ôºâ
      const container = document.getElementById("map-container");
      if (container && container.parentNode) {
        container.parentNode.removeChild(container); // ÂΩªÂ∫ïÁßªÈô§DOMÂÖÉÁ¥†
        console.log("Âú∞ÂõæÂÆπÂô®Â∑≤ÁßªÈô§");
      }

      this.map = null;
      console.log("Âú∞ÂõæÂÆû‰æãÈîÄÊØÅÂêéËÆøÈóÆÔºö", this.map);

      // Âº∫ÂåñÁõ∏ÂÖ≥ÂÆû‰æãÊ∏ÖÁêÜ
      if (this.geocoder) {
        this.geocoder = null;
      }
      this.marker = null;
      if (this.infoWindow) {
        this.infoWindow.destroy(); // Êîπ‰∏∫destroyÊñπÊ≥ï
        this.infoWindow = null;
      }
    }
  },
};
</script>

<style lang="scss" scoped>
